.interest {}

.container {}

.interest__title {}

.title {}

.interest__title--text {}

.interest__content {}

.interest__content__button {
    text-align: center;
    padding-block-end: 8.2%;

    button.interest__content__buttontext {
        padding: 19px 77px;
        background: #04336D 0% 0% no-repeat padding-box;

        a {
            color: #FFFFFF;
        }
    }
}





.interest__content__card {}

.cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 7%;
    margin-inline-start: 6.2%;
    margin-inline-end: 6.2%;
}

.interest__content__card__text {}

.card {
    display: grid;
    grid-template-columns: 1fr 4fr;
    margin-block-end: 7%;
}

.interest__content__card__textimg {
    width: 240px;
    height: auto;
    overflow: hidden;
    position: relative;

    img {
        max-width: 100%;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}

.interest__content__card__textimg {}

.interest__content__card__textinfo {
    padding-inline-start: 4.3%;
    padding-inline-end: 4.3%;
    background: #ECEEEF 0% 0% no-repeat padding-box;

    h3 {
        font-size: 24px;
    }

    p {
        font-size: 16px;
    }

}

@media (max-width: 1024px) {
    .interest__content__card__textinfo {
        padding-inline-end: 5.3%;
        padding-inline-start: 5.3%;

        h3 {
            font-size: 18px;
        }

        p {
            font-size: 12px;
            line-height: 16px
        }
    }

    .cards {
        margin-inline-start: 5.2%;
        margin-inline-end: 5.2%;
        grid-column-gap: 4%;
    }

    .interest__content__card__textimg {
        width: 180px;
    }

    .interest__content__button {
        button.interest__content__buttontext {
            padding: 14px 66px;

            a {
                font-size: 15px;
                letter-spacing: 0.3px;
            }
        }
    }
}

@media (max-width: 768px) {
    .cards {
        grid-template-columns: 1fr;
        margin-inline-end: 0;
    }

    .interest__content__card__textimg {
        width: 50vw;
        height: 50vw;
    }

    .interest__content__card__textinfo {
        h3 {
            font-size: 30px;
        }

        p {
            font-size: 16px;
            line-height: 26px;
        }
    }
}

@media (max-width: 480px) {
    .card {
        grid-template-columns: 1fr;
    }

    .interest__content__card__textimg {
        width: 100%;
        height: 88vw;
    }
}

@media (max-width: 320px) {

    .interest__title--text {
        font-size: 18px;
    }

    .interest__content__card__textinfo {
        h3 {
            font-size: 16px;
        }

        p {
            font-size: 14px;
            line-height: 23px;
        }
    }

}