@charset "UTF-8";

/**
 * Основные стили
 **/

html {
	font-family: 'Merriweather', serif;
	font-size: 16px;
	color: $black;
}

body {
	font-family: 'Merriweather', 'Arial', sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word;
}

.container {
	@include min-max(320px, 1440px, 0);
}

.wrap {
	@include min-max(320px, 1240px, 0px);
}

h1 {
	font-size: 60px;
	color: #fff;
}

h2 {
	font-size: 40px;
}

h3 {
	font-size: 20px;
}

h4 {
	font-size: 16px;	
}

li a,a {
	font-size: 16px;
	color: #D0E5FF;
	opacity: 0.8;
	text-decoration: none;
}



ul {
	list-style: none;
	padding: 0;
}

p {
	font-size: 16px;
	letter-spacing: 0px;
    line-height: 28px;	
}
