.legend {
    background: #ECEEEF 0% 0% no-repeat padding-box;
}

.legend__title {}

.legend__content {}

.legend__content__columns {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-inline: 9.3%;
    grid-column-gap: 3.7%;
}

.legend__content__column {
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 1px 2px #00000038;
    border-radius: 10px;
    opacity: 1;
}

.legend__content__column--space {
    padding: 6%;
    position: relative;
}


// Позиционирование аватара за контейнер
.pos {
    position: absolute;
    top: -30px;
    left: -22px;
}

.legend__content__column--img {
    border-radius: 10px;
    opacity: 1;
    width: 80px;
    height: 80px;
    overflow: hidden;
    position: relative;

    img {
        max-width: 100%;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}

.legend__content__column--txt {
    padding-inline: 7%;
    padding-block-start: 10.3%;
    padding-block-end: 10.3%;
}

.legend__content__button {
    text-align: center;
    padding-block-start: 3.3%;
    padding-block-end: 8.2%;
}

.legend__content__buttontext {
    padding: 20px 23px;
    background: #04336D 0% 0% no-repeat padding-box;

    a {
        font-size: 18px;
        color: #FFFFFF;


    }
}

@media (max-width: 1024px) {
    .legend__title {
        font-size: 32px;
    }

    .legend__content__columns {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        margin-inline: 6%;
    }

    .legend__content__column--space {
        padding: 6%;
        padding-block-end: 0;
    }

    .pos {
        top: -21px;
        left: -12px;
    }

    .legend__content__column--img {
        width: 60px;
        height: 60px;
    }

    .legend__content__column--txt {
        padding-inline: 7%;
        padding-block-start: 10.3%;
        padding-block-end: 5.3%;

        h4 {
            font-size: 13px;
        }

        p {
            font-size: 13px;
            letter-spacing: 0.16px;
            line-height: 20px;
        }
    }

}

@media (max-width: 768px) {
    .legend__content__columns {

        grid-template-columns: 1fr 1fr;

    }

    .legend__content__column {
        margin-block-end: 10%;
    }
}

@media (max-width: 480px) {
    .legend__content__columns {
        grid-template-columns: 1fr;
    }

    .legend__title {
        h2 {
            font-size: 6vw;
        }
    }

    .legend__content__column--txt {
        h4 {
            font-size: 4vw;
        }

        p {
            font-size: 4vw;
        }
    }
}

@media (max-width: 320px) {
    .legend__content__column--txt {
        h4 {
            font-size: 18px;
        }

        p {
            font-size: 12px;
        }
    }

    .legend__content__column--txt {
        padding-inline: 4%;
        padding-block-start: 4.3%;
        padding-block-end: 1.3%;
    }

    .legend__content__buttontext {
        padding: 10px 10px;

        a {
            font-size: 12px;



        }
    }
}
